  methods: {
    //获取列表数据
    getTableList () {
      let vm = this
      let whereObj = {}
      if (vm.searchForm.name) {
        whereObj['name'] = vm.searchForm.name.trim()
      }
      if (vm.searchForm.NO) {
        whereObj['NO'] = vm.searchForm.NO.trim()
      }
      let url = 'api/hc/sys/role/pagelist'
      this.$axios
        .post(url, {
          pageSize: vm.page.pageSize,
          currentPage: vm.page.currentPage,
          whereObj: whereObj
        })
        .then(res => {
          if (res.status === 200) {
            vm.tableData = res.BO
            vm.page.totalNum = res.pageinfo.totalNum
          }
        })
    },
